.stayDetailForm {
  padding: 5px;
  .btn {
    column-gap: 17px;   
  }
  .btnWidth {
    width: 160px;
  }
  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    .btn {
      column-gap: 17px;   
    }
    .btnWidth {
      width: 160px;
    }
}
.btnWidth {
  width: 160px;
}
.contentPart::-webkit-scrollbar {
  width: .3em;
}

.contentPart::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(191, 186, 186, 0.381);
  border-radius: 5%;
}

.contentPart::-webkit-scrollbar-thumb {
  background-color: rgb(209, 204, 204);
  outline: .2px solid rgb(183, 186, 189);
  border-radius: 5%;
}
  hr {
    border-top: 2px solid #e8e9e9;
  }
  .stayStyles {
    color: #1e2528;
  }

}
.defineRoom {
  padding-top: 2%;

  .deleteIconOutline {
    border: 1.5px solid #d2d3d4;
    color: #bf3939;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    font-size: 16px;
    padding: 7px;
    float: center;
    margin-top: 38px;
    font-weight: 600;
  }

  .bsPlusCircleOutline {
    border: 1.5px solid #d2d3d4;
    color: #a27ed7;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    font-size: 16px;
    padding: 7px;
    float: center;
    margin-top: 38px;
    font-weight: 600;
  }

  .bsPlusCircleColor {
    color: #a27ed7;
    margin: 8px;
    padding: auto;
  }
  .tableWrap {
    border: 1px solid#E8E9E9;
    border-radius: 9px;
    overflow: hidden;
    margin-top: 16px;
    width: max-content;
  }
  .table {
    width: 722px;
    
    thead {
      tr {
        th {
          background-color: #E2F8FB;
          color:#2A5067;
          font-family: Inter;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          padding: 14px 16px;
        }
      }
    }
    tbody {
      tr {
        td {
          padding: 8px 16px;
          vertical-align: bottom;
          border-bottom: 0 !important;
        }
      }
    }
  }
}
